<template>
  <div class="flatTime" >
    <div class="flatTime-box" v-for="(item,index) in flatTimeaxisArr" :style="{width:index!=flatTimeaxisArr.length-1?eventbox+eventbox/2+'px':eventbox+'px',height:eventbox+'px'}">
      <div class="flatTime-hang" v-if="!item.time">
        <div class="flatTime-eventbox" :style="{width:eventbox+'px',height:eventbox+'px'}" @click="clicktime(index)">
          <p>{{item.timeevent}}</p>
        <!--  <p>{{item.time}}</p>-->
        </div>
        <div class="flatTime-line" :style="{width:eventbox/2+'px',top:eventbox/2+'px'}"  v-if="index!=flatTimeaxisArr.length-1"></div>
      </div>
      <div class="flatTime-finish" v-if="item.time">
        <div  v-bind:class="item.state==0 ? 'flatTime-overeventbox' : 'flatTime-eventbox'" @click="clicktime(index)"  :style="{width:eventbox+'px',height:eventbox+'px'}">
          <p>{{item.timeevent}}</p>
          <p>{{item.time}}</p>
        </div>
        <div class="flatTime-arrowline" v-if="index!=flatTimeaxisArr.length-1">
          <div class="flatTime-line" :style="{width:eventbox/2-3+'px',top:eventbox/2+'px'}"></div>
          <div class="flatTime-lineaxie" :style="{left:eventbox+eventbox/2-3+'px',top:eventbox/2-2+'px'}"></div>
        </div>
      </div>
      <div class="flatTime-idalog" v-show="index==overtimeindex&&item.state==0&&overtime" :style="{left:eventbox*overtimeindex+'px',top:eventbox+10+'px'}">
        <!--<div class="flatTime-arrow" :style="{left:eventbox/4+'px',top:eventbox/2+20+'px'}"></div>-->
        <div class="flatTime-arrow" :style="{left:eventbox/2-10+'px',top:eventbox-10+'px'}"></div>
        <div class="flatTime-hintsuspen"  :style="{left:-170+'px',top:eventbox+10+'px'}">
          <!--<div style="background: #ddd;height:20px;"></div>-->
          <ul  class="modelStyle clearfix">
            <li style="width:100%;">抽血时间: {{item.data}}</li>
            <li style="width:100%;">首次医疗接触时间  <span class="bgImg">至</span>  快速检测时间: <span style="font-weight:bold;">使用时长 {{item.actualtime}}</span></li>
            <li>质控时长:{{item.qualitytime}}</li>
            <li style="margin-bottom:10px;">超过目标时间:{{item.exceedtargettime}}</li>
            <li style="width:100%;height:88px;line-height:normal;margin-bottom:16px;"><div style="padding-right:10px;float:left;height:88px;line-height: 88px;">原因:</div>
              <textarea class="textarea" v-model="item.timeoutCause"></textarea>
            </li>
            <li style="text-align: center;width:100%;">
              <Button class="flatTime-btn" type="primary" @click="save(item)">确定</Button>
              <Button style="margin-left:10px;" class="flatTime-btn" type="primary" @click="cancel">取消</Button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
