<script>
  import Vue from 'vue'
  let deepAssign = require('deep-assign')
  export default {
    name: "flatTime",
    props: {
      timeaxisArr: {type: Array, default: null},
      timeaxisProps:{type:Object,default: null},
      flatstyle:{type:Object,default: null},
      width:{type:Number,default: 500},
    },
    components: {

    },
    data() {
      return {
          //放数组变量
        flatTimeaxisArr:[
          /* {data:"18",time:"23:05",timeevent:"发病时间",state:"0",isshow:false},
           {data:"18",time:"",timeevent:"呼救时间",state:"1", isshow:false},
           {data:"19",time:"",timeevent:"首次医疗接触",state:"1", isshow:false},
           {data:"19",time:"",timeevent:"首次心电图解读",state:"-1",isshow:false}*/
        ],
        //超时原因
        timeoutCause:"",
        //配置项
        setting:{
          //数组配置
          data:{
            timeevent:"timeevent", //发生的事件
            state:"state",  //状态 1正常 2不正常 默认为1正常
            time:"time",  //发生事件的时间
            data:"data",  //年月日时间
            actualtime:"actualtime",//实际使用时长
            qualitytime:"qualitytime",//质控时长
            exceedtargettime:"exceedtargettime", //超过目标时间
            timeoutCause:"timeoutCause", //超时原因
            },
        },
        eventbox:85.7, //时间球宽度
        //显示抽血显示弹框
        overtime:false,
        //显示抽血 的当前index
        overtimeindex:"",
      }
    },
    created: function () {
    },
    mounted: function () {
      this.inittimeAxis(this.timeaxisArr)
    },
    methods: {
        //计算宽度
      calculationWidth:function(){
         // console.log(this.width)
          //箭头线是事件球宽度的一半，将事件球和箭头线看作一个整体分成几份
          let boxnum=this.flatTimeaxisArr.length+(this.flatTimeaxisArr.length-1)/2
        let eventbox=this.width/boxnum  //一份的宽度
       // console.log(this.flatTimeaxisArr.length)
        if(eventbox<85.7){  //事件球的最小宽度是100
          this.eventbox=85.7
        }else if(eventbox>115){  //事件球的最小宽度是100
          this.eventbox=115
        }else{
          this.eventbox=eventbox
        }
        //console.log(this.eventbox)
      },
      //初始化
      inittimeAxis:function(arrnode){
          console.log(arrnode)
        this.flatTimeaxisArr=arrnode
        this.setting= deepAssign(this.setting,this.timeaxisProps)
        for(let i in this.setting.data){
          if(i!=this.setting.data[i]){
             // console.log(i)
            //console.log(this.setting.data[i])
            for(let j=0;j<this.flatTimeaxisArr.length;j++){
                 // console.log(this.flatTimeaxisArr[j][this.setting.data[i]])
              this.flatTimeaxisArr[j][i]=this.flatTimeaxisArr[j][this.setting.data[i]]
            }
           // console.log( this.flatTimeaxisArr)
          }
        }
        this.calculationWidth()
      },
      //点击事件
      clicktime:function(index){
          //alert(index)
       // this.flatTimeaxisArr[index].isshow=true
        /*
        * index 点击事件球数据的下标
        * this.flatTimeaxisArr[index] 当前事件球的数据
        * */
        this.$emit("flat-clicktime",[index,this.flatTimeaxisArr[index]])
      },
      //显示抽血时间超时的弹框
      hintSuspension(index,nodedata,flag){
        console.log(nodedata)
        //Vue.set(this.flatTimeaxisArr[index],"state","0");
        Vue.set(this.flatTimeaxisArr,index,nodedata);
        //this.$forceUpdate()
        this.overtimeindex=index
        this.overtime=flag
        //this.$forceUpdate(); //强制刷新
      },
      //点击事件球改变颜色
      changecolor(index,nodetime){
       // this.flatTimeaxisArr[index].time=nodetime
        Vue.set(this.flatTimeaxisArr[index],"time",nodetime);
        this.$forceUpdate(); //强制刷新
      },
      //点击弹框确定   datanode当前时间点的数据
      save(datanode){
          /*
          * datanode,当前时间点的数据
          * this.timeoutCause 原因
          * */
        this.overtime=false
        this.overtimeindex=""
        this.timeoutCause=""
        this.$emit("flat-hintok",[datanode,this.timeoutCause])
      },
      //点击取消
      cancel(){
          //清空
        this.overtime=false
        this.overtimeindex=""
        this.timeoutCause=""
      },
    }
  }
</script>




