<template>
  <div>
    <div class="top">
      <ul class="patInformation">
        <li>
          <span class="patName">{{patientinfo.padPatInfoView.name}}</span>
          <span class="patGender">{{patientinfo.padPatInfoView.gender}}</span>
          <span class="patAge" v-if="patientinfo.padPatInfoView.age_value">{{patientinfo.padPatInfoView.age_value}}岁</span>
        </li>
        <li><Button class="frameBtn" @click="cancelPro">取消任务</Button></li>
      </ul>
      <div class="time">
        <flatTimeaxis ref="flattime" :width="timeWidth"
                      @flat-clicktime="flatClicktime"
                      @flat-hintok="flatHintok"
                      :timeaxisArr="flatTimeaxisArr"
                      :timeaxisProps="flatTimeaxisProps"
                      ></flatTimeaxis>
      </div>
    </div>
    <div class="bottom" :style="{marginTop:'10px',height:botHeight}">
      <router-view ref="luyou" @time-change="timeAxisChange"></router-view>
    </div>
    <div style="background:#fff;text-align: center">
      <div class="versionBtn">
        <Button type="primary" class="button" :disabled="util.disabledFlag" @click="applicationRoom">申请导管室</Button>
        <Button type="primary" class="button" :disabled="util.disabledFlag">送达急诊科</Button>
        <Button type="primary" class="button" :disabled="util.disabledFlag"  @click="saveAllPatientInfo">保存</Button>
        <Button type="primary"  v-if="AidButton" :disabled="util.disabledFlag" class="button" @click="firstAddTip">完成急救</Button>
      </div>
    </div>
    <!--确认急救-->
    <Modal
      v-model="aidComplate"
      title="提示"
      class-name="vertical-center-modal"
      :styles="{width:'400px',height:'200px'}"
      @on-ok="firstAidOk"
      @on-cancel="cancel">
      <!--<img style="width:40px;height:40px;margin:20px 10px 0 0;" :src=img />-->
      <p style="display: inline-block;width: 100%;text-align: center">确定急救完成？</p>
    </Modal>
    <!--取消任务-->
    <modal-dalog ref="cancelTaskModal" :wid="cancelWid" @oktip-modal="cancelTaskModal" :dalogTitle="cancelTaskTitle">
      <dialog-tips  ref="cancelTask" slot="dalogslot"></dialog-tips>
    </modal-dalog>
    <!--申请导管室-->
    <modal-dalog ref="applicationRoomModal" @oktip-modal="applicationRoomModal">
      <agree-tips slot="dalogslot"></agree-tips>
    </modal-dalog>
    <!--同意谈话-->
    <modal-dalog ref="agreeTalkModal" @oktip-modal="agreeTalkModal" :dalogTitle="agreeTalkTitle">
      <agree2-tips slot="dalogslot"></agree2-tips>
    </modal-dalog>
  </div>
</template>

<script>
  import Vue from 'vue'
  import flatTimeaxis from "@/components/timeAxis/flatTimeaxis"
  Vue.component("dialog-tips", {
    template:
    '<div style="font-size:14px;color:#333;width:100%;height:195px;padding:10px;margin:auto;overflow: hidden">' +
      '<p style="margin-bottom:10px;">确定要取消任务吗？</p>'+
      '<p>取消任务的原因:</p>' +
      '<textarea v-model="cancelResult" style="margin-top:8px;height:124px;width:100%;resize:none;border-radius:0;border:1px solid #bfcbe5;"></textarea>' +
    "</div>",
    data() {
      return {
        img: require("../../../static/img/warn.png"),
        cancelResult:"",
      };
    }
  });
  Vue.component("agree-tips", {
    template:
    '<div style="width:100%;line-height:88px;height:88px;margin:auto;overflow: hidden">' +
    '<p style="float:left;margin-left:100px;"><img style="width:40px;height:40px;margin:20px 10px 0 0;" :src=img /></p>' +
    '<p style="float:left">同意PCI手术吗?</p>' +
    "</div>",
    data() {
      return {
        img: require("../../../static/img/warn.png")
      };
    }
  });
  Vue.component("agree2-tips", {
    template:
    '<div class="clearfix" style="width:100%;height:88px;margin:auto;overflow: hidden">' +
    '<p style="float:left;"><img style="width:40px;height:40px;margin:20px 10px 0 0;" :src=img /></p>' +
    '<p style="float:left;width:300px;margin-top:20px;">心电图诊断为STEMI阳性，同意谈话可提前启动导管室?</p>' +
    "</div>",
    data() {
      return {
        img: require("../../../static/img/warn.png")
      };
    }
  });
  export default {
    name: "left-frame",
    components:{
      flatTimeaxis
    },
    data(){
      return {
        aidComplate:false,//完成急救确认提示框
        AidButton:false,//急救按钮
        botHeight:"200px",
        timeWidth:600,
        cancelTaskTitle:"取消任务",
        agreeTalkTitle:"同意谈话？",
        flatTimeaxisArr:[
          {data:"",datatime:"",timeevent:"发车时间",state:"1",isshow:false},
          {data:"",datatime:"",timeevent:"到场时间",state:"1", isshow:false},
          {data:"",datatime:"",timeevent:"首次医疗接触",state:"1", isshow:false},
          {data:"",datatime:"",timeevent:"抽血时间",state:"1",isshow:false,actualtime:"",
            qualitytime:"20分钟",exceedtargettime:"",timeoutCause:"",},
          {data:"",datatime:"",timeevent:"首次药时间",state:"1",isshow:false}
        ],
        cancelWid:"530",
        flatTimeaxisProps:{
           //数组配置
          data:{
             timeevent:"timeevent", //发生的事件
             state:"state",  //状态 1正常 2不正常 默认为1正常
             time:"datatime",  //发生事件的时间
             data:"data",  //抽血时间 年月日时分时间
             actualtime:"actualtime",//实际使用时长
             qualitytime:"qualitytime",//质控时长
             exceedtargettime:"exceedtargettime", //超过目标时间
             timeoutCause:"timeoutCause", //超时原因
           },
         },
      }
    },
    created(){
      var pThis=this;
      pThis.botHeight=$(window).height()-36-30-90-54+"px";
      $(window).resize(function () {
        pThis.botHeight=$(window).height()-36-30-90-54+"px";
      });
      if($(window).width()>1000){
        pThis.timeWidth=700
      }else{
        pThis.timeWidth=600
      }
    },
    mounted() {
      var pThis=this;
      pThis.timeChange("start_car_time",0);
      pThis.timeChange("arrived_hospital_time",1);
      pThis.timeChange("first_mc_time",2);
      pThis.timeChange("sampling_time",3);
      pThis.timeChange("acs_delivery_time",4);
    },
    watch: {
      $route(to, from) {
        if(this.$route.name =='timeOverView'){
          this.AidButton = true;
        }else{
          this.AidButton =false;
        }
      }
    },
    methods:{
      firstAddTip(){
        this.aidComplate = true;
      },
      firstAidOk(){
          this.aidComplate = false;
        this.firstAddPatientInfo();
        clearInterval(this.location.timer);
        this.location.timer='';
      },
      cancel(){
        this.aidComplate = false;
      },
      //子组件触发日历chang时间
      timeAxisChange(time,str,index){
        var pThis=this;
        if(time){
          if(typeof time=="object"){
            time=pThis.dateTime(time,19)
          }
        }else{
          return;
        }
        time=time.substr(11,5);
        pThis.$refs.flattime.changecolor(index,time); //点击事件球，若完成改变颜色
        // if(str=="sampling_time"){
        //   if(pThis.patientinfo.padTimeView.first_mc_time){
        //     if(typeof pThis.patientinfo.padTimeView.first_mc_time=="object"){
        //       pThis.patientinfo.padTimeView.first_mc_time=pThis.dateTime(pThis.patientinfo.padTimeView.first_mc_time)
        //     }
        //     pThis.dateTimeDif(pThis.patientinfo.padTimeView.first_mc_time,time,index,100,false);
        //   }else{
        //     pThis.$refs.flattime.changecolor(index,time);
        //   }
        // }else{
        //   pThis.$refs.flattime.changecolor(index,time); //点击事件球，若完成改变颜色
        // }
      },
      //页面加载时----时间轴显示问题
      timeChange(name,index){
        var pThis=this;
        if(pThis.patientinfo.padTimeView[name]){
          if(typeof pThis.patientinfo.padTimeView[name]=="object"){
            pThis.patientinfo.padTimeView[name]=pThis.dateTime(pThis.patientinfo.padTimeView[name],19);
          }
          pThis.flatTimeaxisArr[index].datatime=pThis.patientinfo.padTimeView[name].substr(11,5);
          // if(name=="sampling_time"){
          //   if(pThis.patientinfo.padTimeView.first_mc_time){
          //     if(typeof pThis.patientinfo.padTimeView.first_mc_time=="object"){
          //       pThis.patientinfo.padTimeView.first_mc_time=pThis.dateTime(pThis.patientinfo.padTimeView.first_mc_time,19);
          //     }
          //     pThis.dateTimeDif(pThis.patientinfo.padTimeView.first_mc_time,pThis.patientinfo.padTimeView.sampling_time,index,100,false);
          //   }
          // }else{
          //   pThis.$refs.flattime.changecolor(index,pThis.flatTimeaxisArr[index].datatime);
          // }
          pThis.$refs.flattime.changecolor(index,pThis.flatTimeaxisArr[index].datatime);
        }
      },
        //完成急救
      firstAddPatientInfo(){
        var pThis=this;
        var data = {
          pk_task:pThis.patientinfo.padPatInfoView.pk_task
        };
        if(data.pk_task==''){
          pThis.$toast.center('fail.png','未选择急救任务');
        }else {
          pThis.$http.post(pThis.host("task/changTaskStateComplete"), data)
            .then(function (response) {
              let datajson = response.data;
              if (datajson.state == "1") {
                pThis.$toast.center('success.png', '保存' + datajson.msg);
                pThis.emityPatientInfo();
                pThis.$router.push("/home");
                pThis.menuActiveName = '1-1';
                pThis.emityPatientInfo();
              }
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      },
      //完成急救清空患者信息
      emityPatientInfo(){
           var pThis=this;
      for( var value in pThis.patientinfo){
        for(var val in pThis.patientinfo[value]){
          pThis.patientinfo[value][val] ='';
        }
      }
      },
      //时间轴----点击事件
      flatClicktime(arr){
        var pThis=this;
        if(pThis.util.disabledFlag){
          return false;
        }
        pThis.$http.post(pThis.host("public/systemTime"),{format:"yyyy-MM-dd HH:mm"})
          .then(function (response) {
            let datajson = response.data;
            if (datajson.state == "1") {
              if(arr[0]==0){
                Vue.set(pThis.patientinfo.padTimeView,'start_car_time',datajson.results[0].systemTime);
                pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
              }else if(arr[0]==1){
                Vue.set(pThis.patientinfo.padTimeView,'arrived_hospital_time',datajson.results[0].systemTime);
                pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
              }if(arr[0]==2){
                Vue.set(pThis.patientinfo.padTimeView,'first_mc_time',datajson.results[0].systemTime);
                pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
              }if(arr[0]==3){
                Vue.set(pThis.patientinfo.padTimeView,'sampling_time',datajson.results[0].systemTime);
                pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
                // //如果首次医疗时间存在则进行比较
                // if(pThis.patientinfo.padTimeView.first_mc_time){
                //   if(typeof pThis.patientinfo.padTimeView.first_mc_time=="string"){
                //     pThis.dateTimeDif(pThis.patientinfo.padTimeView.first_mc_time,datajson.results[0].systemTime,arr[0],100,true)
                //   }else if(typeof pThis.patientinfo.padTimeView.first_mc_time=="object"){
                //     pThis.patientinfo.padTimeView.first_mc_time=pThis.dateTime(pThis.patientinfo.padTimeView.first_mc_time,19);
                //     pThis.dateTimeDif(pThis.patientinfo.padTimeView.first_mc_time,datajson.results[0].systemTime,arr[0],100,true)
                //   }
                // }else{
                //   Vue.set(pThis.patientinfo.padTimeView,'sampling_time',datajson.results[0].systemTime);
                //   pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                //   pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
                // }
              }if(arr[0]==4){
                Vue.set(pThis.patientinfo.padTimeView,'acs_delivery_time',datajson.results[0].systemTime);
                pThis.flatTimeaxisArr[arr[0]].datatime=datajson.results[0].systemTime.substring(11);
                pThis.$refs.flattime.changecolor(arr[0],arr[1].datatime); //点击事件球，若完成改变颜色
              }
            }
          }).catch(function (error) {
            console.log(error);
          });


      },
      //将毫秒转成分钟
      haomianChangeTime(t){
        var minutes = (t % 3600000) / 60000;
        return minutes
      },
      //抽血时间---超过标准时间弹框--确认按钮
      flatHintok(dataarr){
        var pThis=this;
        pThis.patientinfo.padCheckInfoView.sampling_overtime_reason=dataarr[0].timeoutCause;
      },
      agreeCancel() {
        this.$refs.agreeCancel.openmodal();
      },
      //取消任务按钮--打开弹框
      cancelPro(){
        var pThis=this;
        pThis.$refs.cancelTaskModal.openmodal();
      },
      //取消弹框---确认按钮
      cancelTaskModal(){
        var pThis=this;
        // console.log(pThis.$refs.cancelTask.cancelResult)
        var params={
          "pk_task": pThis.patientinfo.padPatInfoView.pk_task,
          "cancel_reason": pThis.$refs.cancelTask.cancelResult,
          "ts": pThis.patientinfo.padPatInfoView.ts_task
        };
        pThis.$http.post(pThis.host("task/changTaskStateCannel"),params)
          .then(function (response) {
            let datajson = response.data;
            let backjson={"state":datajson.state,"msg":datajson.msg};
            pThis.backLogin(backjson);
            if (datajson.state == "1") {
              pThis.$toast.center('success.png',"取消"+datajson.msg);
              Vue.set(pThis.util,'menuActiveName','1-1');
              pThis.$router.push({ path: "home" });
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      //申请导管室按钮--打开弹框
      applicationRoom(){
        var pThis=this;
        pThis.$refs.applicationRoomModal.openmodal();
      },
      //申请导管室按钮--打开弹框确认按钮
      applicationRoomModal(){
        var pThis=this;
        pThis.$refs.agreeTalkModal.openmodal();
      },
      //同意弹框--确认按钮
      agreeTalkModal(){},
      //日期转换
      format(time, value) {
        var date = new Date(time);
        var seperator1 = "-";
        var seperator2 = ":";
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) { month = "0" + month; }
        if (strDate >= 0 && strDate <= 9) { strDate = "0" + strDate; }
        if(isNaN(date.getFullYear())){
          return time;
        }else {
          var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes();
//        console.log(currentdate)
          return currentdate;
        }
      },
      //dateTime
      dateTime(date,length){
        if(length==10){
          if(typeof date =='object'){
            var years=date.getFullYear();
            var months=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
            var days=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
            return years+"-"+months+"-"+days;
          }
        }else{
          if(typeof date =='object'){
            var year=date.getFullYear();
            var month=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
            var day=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
            var hours=date.getHours() <=9 ?"0"+date.getHours():date.getHours();
            var mins=date.getMinutes() <=9 ?"0"+date.getMinutes():date.getMinutes();
            var seds=date.getSeconds() <=9 ?"0"+date.getSeconds():date.getSeconds();
            return year+"-"+month+"-"+day+" "+hours+":"+mins+":"+seds;
          }
        }
        return date
      },
      //将后台返回的null转成空串
      nullChangeVoid(view){
        var pThis=this;
        for(var key in pThis.patientinfo[view]){
          if(pThis.patientinfo[view][key]==null){
            pThis.patientinfo[view][key]="";
          }
        }
      },
      //保存
      saveAllPatientInfo(){
        var pThis=this;
        //时间总览
        console.log(pThis.patientinfo.padDiaInfoView);
        for(var key in pThis.patientinfo.padTimeView){
          if(pThis.patientinfo.padTimeView[key]==null){
            pThis.patientinfo.padTimeView[key]="";
          }
          if(typeof pThis.patientinfo.padTimeView[key]=="object"){
            pThis.patientinfo.padTimeView[key]=pThis.dateTime(pThis.patientinfo.padTimeView[key],19)
          }
        }
        //诊断治疗
        for(var keys in pThis.patientinfo.padDiaInfoView){
          if(pThis.patientinfo.padDiaInfoView[keys]==null){
            pThis.patientinfo.padDiaInfoView[keys]="";
          }
          if(typeof pThis.patientinfo.padDiaInfoView[keys]=="object"){
            pThis.patientinfo.padDiaInfoView[keys]=pThis.dateTime(pThis.patientinfo.padDiaInfoView[keys],19)
          }
        }
        //基本信息
        for(var keyb in pThis.patientinfo.padPatInfoView){
          if(pThis.patientinfo.padPatInfoView[keyb]==null){
            pThis.patientinfo.padPatInfoView[keyb]="";
          }
          if(typeof pThis.patientinfo.padPatInfoView[keyb]=="object"){
            if(keyb=='birthday'){
              pThis.patientinfo.padPatInfoView[keyb]=pThis.dateTime(pThis.patientinfo.padPatInfoView[keyb],10);
              continue;
            }
            pThis.patientinfo.padPatInfoView[keyb]=pThis.dateTime(pThis.patientinfo.padPatInfoView[keyb],19)
          }
        }
        //血压验证
        if(!pThis.util.isBloodFlag){
          pThis.$toast.center('warn.png',pThis.util.bloodMessage);
          return;
        }
        pThis.$http.post(pThis.host('task/saveTaskList'),pThis.patientinfo)
          .then(function (res) {
            let datajson = res.data;
            let backjson={"state":datajson.state,"msg":datajson.msg};
            pThis.backLogin(backjson);
            if(datajson.state=='1'){
              pThis.$toast.center('success.png','保存'+datajson.msg);
               pThis.patientinfo=datajson.results[0];
              Vue.set(pThis.patientinfo,'padPatInfoView',datajson.results[0].padPatInfoView);
              Vue.set(pThis.patientinfo,'padCheckInfoView',datajson.results[0].padCheckInfoView);
              Vue.set(pThis.patientinfo,'padDiaInfoView',datajson.results[0].padDiaInfoView);
              Vue.set(pThis.patientinfo,'padTimeView',datajson.results[0].padTimeView);
              pThis.nullChangeVoid('padPatInfoView');
              pThis.nullChangeVoid('padCheckInfoView');
              pThis.nullChangeVoid('padDiaInfoView');
              pThis.nullChangeVoid('padTimeView');
            }
          }).catch(function (error) {
          console.log(error)
        });
      },
      //根据时间差，做相应的处理
      //startTime和endTime为string类型：标准19位---time点击是系统时间
      // index位于时间轴第几个（默认为0）---抽血和医疗间隔标准时间为standerTime
      dateTimeDif(startTime,endTime,index,standerTime,flag){
        var pThis=this;
        // debugger
        //将时间类型转成毫秒
        var start=new Date(startTime).getTime();
        var end=new Date(endTime).getTime();
        if(end-start>standerTime){//大于标准时间限制
          var json={data:"",datatime:"",timeevent:"抽血时间",state:"",isshow:false,actualtime:"",
            qualitytime:"20分钟",exceedtargettime:"",timeoutCause:""};
          json.state="0";//时间超时状态
          json.data=endTime;
          json.datatime=endTime.substr(11,5);//时间轴显示时间
          json.timeoutCause=pThis.patientinfo.padCheckInfoView.sampling_overtime_reason;//时间超时原因
          json.actualtime=Math.ceil(pThis.haomianChangeTime(end-start))+"分钟";//首次医疗和抽血时间差---将毫秒转成分钟
          json.exceedtargettime=Math.ceil(pThis.haomianChangeTime(end-start-standerTime))+"分钟";//比标准时间超多少---将毫秒转成分钟
          pThis.$refs.flattime.hintSuspension(index,json,flag);
          pThis.$refs.flattime.changecolor(index,json.datatime);
          Vue.set(pThis.patientinfo.padTimeView,'sampling_time',endTime);
          Vue.set(pThis.util,'warnFlag',true);
        }else{
          var jsons={data:"",datatime:"",timeevent:"抽血时间",state:"1",isshow:false,actualtime:"",
            qualitytime:"20分钟",exceedtargettime:"",timeoutCause:""};
          Vue.set(pThis.patientinfo.padTimeView,'sampling_time',endTime);
          jsons.datatime=endTime.substr(11,5);
          pThis.$refs.flattime.hintSuspension(index,jsons,flag);
          pThis.$refs.flattime.changecolor(index,jsons.datatime);
          Vue.set(pThis.util,'warnFlag',false);
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  @media screen and (max-width:1000px){
    .patInformation {flex:0 0 170px;width: 170px;}
    .top{height: 90px;}
  }
  @media screen and (min-width:1001px){
    .patInformation {flex:0 0 200px;width: 200px;}
    .top{
      height: 90px;
    }
  }
  //页面底部按钮样式
  .versionBtn{
    background:#fff;padding:10px 0 10px 0;width:500px;margin:0 auto;
  }
  .ivu-modal-body{
    text-align: center;
  }
  //页面按钮样式设为===微软雅黑
  .button{margin-left:10px;height:34px;line-height:34px;width:110px;font-size:14px;padding:0;}
  .top {
    background-color: white;
    padding-left: 14px;display:flex;padding-top:2px;
    .time {
     position:relative;flex:1;
    }
    .patInformation {
      font-family:"微软雅黑";
      font-size:18px;
      color:#333;
      font-weight:bold;
      margin-top: 14px;
      /*width: 200px;*/
      display: inline-block;
      .patGender {
        margin-left: 10px;
      }
      .patAge {
        margin-left: 10px;
      }
    }
    .timeButton {
      display: inline-block;
    }
    .frameBtn {
      border:1px solid #4c77d4;
      color: #466ec5;
      background-color: white;
      width: 110px;
      height: 34px;
      margin-top: 6px;
      font-size:14px;
    }
  }
</style>



